* {
  background-color: #000000;
  color: white;
}

.card {
  background-color: black;
  color: white;
  width: 150px;
  position: relative;
  font-weight: 600;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  border: 1px solid white;
  box-shadow: 0px 5px 17px -7px rgba(223, 215, 215, 0.95);
  border-radius: 10px;
  transition: transform 100ms ease-in;
  cursor: pointer;
  filter: brightness(90%);
  margin-bottom: 20px;
}
.card:hover {
  background-color: black;
  color: white;
  transform: scale(1.19);
  filter: brightness(125%);
}
.row-1,
.row-2 {
  border-bottom: 0.25px solid rgb(98, 97, 97);
  border-top: 0.25px solid rgb(98, 97, 97);
  padding: 15px;
}

.row-3 {
  /* border-bottom: 0.25px solid rgb(98, 97, 97); */
  padding-top: 10px;
  padding-bottom: 15px;
  
}
.spin {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
ion-spinner {
  width: 250px;
  height: 250px;
  stroke: #444;
  fill: #222;
}
