.statelist {
  background-color: black;
  border-bottom: 0.25px solid rgb(98, 97, 97);
  border-top: 0.25px solid rgb(98, 97, 97);
  padding: 15px;
}

@media (min-width: 0px) {
  .cell-class {
    border-color: rgb(0, 0, 0);
    border-width: 0.005em;
    border-style: solid;
    margin-bottom: -1px;
    transition: transform 100ms ease-in;
  }
  .cell-class:hover {
    background-color: black;
    color: blue;
    cursor: pointer;
  }

  .cell-class1,
  .cell-class2,
  .cell-class3,
  .cell-class4 {
    border-color: rgb(0, 0, 0);
    border-width: 0.005em;
    border-style: solid;
    margin-bottom: -1px;
    cursor: pointer;
  }
  .cell-class1:hover {
    background-color: #ffc409;
    color: black;
  }

  .cell-class2:hover {
    background-color: #eb445a;
    color: black;
  }

  .cell-class3:hover {
    background-color: #3880ff;
  }

  .cell-class4:hover {
    background-color: #2dd36f;
    color: black;
  }
}
