.district{
  background: black;
}
.card {
  position: relative;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 0px 5px 17px -7px rgba(12, 2, 3, 0.95);
  border-radius: 10px;
  transition: transform 100ms ease-in;
  cursor: pointer;
  filter: brightness(90%);
  margin-bottom: 20px;
}
.card:hover {
  background-color: black;
  color: white;
  transform: scale(1.09);
  filter: brightness(125%);
}
.row-1,
.row-2 {
  border-bottom: 0.25px solid rgb(98, 97, 97);
  border-top: 0.25px solid rgb(98, 97, 97);
  padding: 15px;
}